/**
 * API URL yardmc fonksiyonlar
 */

/**
 * API URL'yi protokole ve ortama gre ayarlayan yardmc fonksiyon
 * @returns {string} API URL
 */
export const getApiUrl = () => {
  // Her zaman .env dosyasndaki URL'yi kullan
  let apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:8080';
  
  // Eer sayfa HTTPS zerinden yklendiyse ve API URL'si HTTP ise, HTTPS'e evir
  if (window.location.protocol === 'https:' && apiUrl.startsWith('http:')) {
    apiUrl = apiUrl.replace('http:', 'https:');
  }
  return apiUrl;
};

/**
 * API endpoint URL'si oluturan yardmc fonksiyon
 * @param {string} endpoint - API endpoint (rn. "/api/chat")
 * @returns {string} Tam API URL'si
 */
export const getApiEndpoint = (endpoint) => {
  const baseUrl = getApiUrl();
  // Eer baseUrl bosa (greceli URL kullanlyorsa), dorudan endpoint'i dndr
  if (!baseUrl) return endpoint;
  
  // URL birletirme - ift slash olmamasn sala
  if (baseUrl.endsWith('/') && endpoint.startsWith('/')) {
    return `${baseUrl}${endpoint.substring(1)}`;
  }
  
  if (!baseUrl.endsWith('/') && !endpoint.startsWith('/')) {
    return `${baseUrl}/${endpoint}`;
  }
  
  return `${baseUrl}${endpoint}`;
};
